import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Home } from './pages/Home';
import { Work } from './pages/Work';

import './App.css';

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path='/' component={() => <Home />} />
        <Route exact path='/work' component={() => <Work />} />
      </Switch>
    </Router>
  );
}

export default App;
